@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700&display=swap");

:root {
	--main-color: #e38528 !important;
}

* {
	font-family: "Roboto", sans-serif;
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	outline: none;
	border: none;
	text-decoration: none;
	text-transform: capitalize;
	transition: all 0.2s linear;
	line-height: 1.5;
}

html {
	font-size: 62.5%;
	overflow-x: hidden;
	scroll-behavior: smooth;
}

body {
	background: rgb(40, 68, 68);
	padding-left: 30rem;
}

section {
	padding: 1rem 5%;
	min-height: 100vh;
}

.heading {
	font-size: 4rem;
	padding-bottom: 9rem;
	color: #111;
	text-transform: uppercase;
	margin-top: 8%;
	padding-left: 30px;
}

.heading span {
	color: var(--main-color);
	text-transform: uppercase;
	text-align: center !important;
	justify-content: center !important;
	align-items: center !important;
}

.btn {
	display: inline-block;
	margin-top: 1rem;
	padding: 0.8rem 3rem;
	background: var(--main-color) !important;
	color: #fff !important;
	cursor: pointer;
	font-size: 1.7rem;
}

.btn:hover {
	background: #111;
	letter-spacing: 0.2rem;
}

header {
	position: fixed;
	top: 0;
	left: 0;
	bottom: 0;
	background: #222;
	display: flex;
	align-items: center;
	justify-content: space-between;
	z-index: 1000;
	flex-flow: column;
	padding: 8rem 2rem;
	width: 30rem;
	text-align: center;
}

/* header  */
.logo {
	text-transform: uppercase;
	color: #fff;
	font-size: 3rem;
	font-weight: bolder;
	margin-top: 80px;
}

header .logo span {
	text-transform: uppercase;
	color: var(--main-color);
}
/* .navbar {
	height: 10px;
} */
header .navbar,
.navListOptions {
	display: block;
	font-size: 2rem !important;
	color: #fff;
	margin: 2rem 0;
	justify-content: center !important;
}
header .navbar a:hover,
.navListOptions:hover {
	letter-spacing: 0.2rem;
	color: var(--main-color);
}
.AppsGrid {
	/* margin-top: 450px; */
}
.follow {
	font-size: 3.5rem !important;
	color: #fff;
	margin: 0 rem;
}

header .follow a:hover {
	color: var(--main-color);
}

#menu-bars {
	position: fixed;
	top: 1rem;
	right: 1rem;
	z-index: 10000;
	background: var(--main-color);
	color: #fff;
	border-radius: 0.5rem;
	padding: 1rem 1.5rem;
	font-size: 3rem;
	cursor: pointer;
	display: none;
}

.cursor-1 {
	position: absolute;
	top: 0;
	left: 0;
	height: 1rem;
	width: 1rem;
	background: var(--main-color);
	pointer-events: none;
	transform: translate(-50%, -50%);
	border-radius: 50%;
	z-index: 10000;
}

.cursor-1.active {
	height: 7rem;
	width: 7rem;
	opacity: 0.3;
}

.cursor-2 {
	position: absolute;
	top: 0;
	left: 0;
	height: 4rem;
	width: 4rem;
	border: 0.1rem solid var(--main-color);
	pointer-events: none;
	transform: translate(-50%, -50%);
	border-radius: 50%;
	z-index: 10000;
	transition: 0.3s linear;
}

.cursor-2.active {
	display: none;
}

.home {
