.skills-bar {
	padding-left: 30px;
	/* 25px 30px; */
	float: right;
	margin-top: 5px;
	width: 800px;
}
.skills-bar .bar {
	margin: 5px 0;
}
.skills-bar .bar .info span {
	font-size: 2rem;
}
.skills-bar .bar .info {
	margin-bottom: 10px;
	color: white;
}
.skills-bar .bar .progress-line {
	position: relative;
	height: 10px;
	width: 460px;
	background: #f0f0f0;
	border-radius: 10px;
	transform: scaleX(0);
	transform-origin: left;
	animation: animate 1s cubic-bezier(1, 0, 0.5, 1) forwards;
}
.bar .progress-line span {
	position: absolute;
	height: 100%;
	border-radius: 10px;
	transform: scaleX(0);
	transform-origin: left;
	animation: animate 1s 1s cubic-bezier(1, 0, 0.5, 1) forwards;
}
@keyframes animate {
	100% {
		transform: scaleX(1);
	}
}
.progress-line .html {
	width: 80%;
}
.progress-line .css {
	width: 70%;
}
.progress-line .bootstrap {
	width: 82%;
}
.progress-line .javascript {
	width: 62%;
}
.progress-line .c {
	width: 90%;
}
.progress-line .mysql {
	width: 60%;
}
.progress-line .dotnet {
	width: 60%;
}
.progress-line .react {
	width: 60%;
}
.bar .progress-line span::before {
	position: absolute;
	content: "";
	height: 0;
	right: 0;
	top: -14px;
	width: 0;
	border: 7px solid transparent;
	border-bottom-width: 0px;
	border-right-width: 0px;
	border-top-style: #f0f0f0;
	border-top-color: #07374a;
}
.bar .progress-line span::after {
	position: absolute;
	right: 0;
	top: -28px;
	color: #f0f0f0;
	font-size: 12px;
	font-weight: 700;
	background: #07374a;
	padding: 1px 8px;
	border-radius: 3px;
}
.progress-line .html::after {
	content: "80%";
}
.progress-line .css::after {
	content: "70%";
}
.progress-line .bootstrap::after {
	content: "82%";
}
.progress-line .javascript::after {
	content: "62%";
}
.progress-line .c::after {
	content: "90%";
}
.progress-line .mysql::after {
	content: "60%";
}
