.wrapper {
	display: flex;
}
.wrapper .static-txt {
	color: #fff;
	font-size: 40px;
	font-weight: 400;
}
.wrapper .dynamic-txts {
	margin-left: 15px;
	height: 70px;
	line-height: 40px;
	overflow: hidden;
}
.dynamic-txts li {
	list-style: none;
	color: #fc6d6d;
	font-size: 40px;
	font-weight: 500;
	position: relative;
	top: 0;
	animation: slide 5s steps(4) infinite;
}
@keyframes slide {
	100% {
		top: -260px;
	}
}
.dynamic-txts li span {
	position: relative;
	margin: 10px 20;
	line-height: 65px;
}
.dynamic-txts li span::after {
	content: "";
	left: 0;
	height: 180%;
	width: 150%;
	background-color: rgb(40, 68, 68);
	animation: typing 3s steps(10) infinite;
}
@keyframes typing {
	100% {
		left: 110%;
		margin: 0 30px 0 30px;
	}
}

/* media queries  */
@media (max-width: 991px) {
	html {
		font-size: 55%;
	}

	body {
		padding: 0;
	}

	#menu-bars {
		display: initial;
	}

	header {
		left: -120%;
	}

	header.active {
		left: 0%;
	}

	.cursor-1,
	.cursor-2 {
		display: none;
	}
}

@media (max-width: 450px) {
	html {
		font-size: 50%;
	}

	header {
		width: 100%;
	}

	.experience .box-container .box {
		padding-right: 0;
	}
}
